import {publicing,listing} from '../../api/api.js'
import {getAddress,deleteAddress,addAddress} from '../../api/request.js'

import modal  from '../../element/modal.vue'
export default{
	components:{modal},
	data() {
		return {
			addressList: [],
			userInfo:{}
		}
	},
	methods:{
		//获取地址信息的网络请求
		getAdderss_method(){
			if(this.userInfo){
				let userid = this.userInfo.id;
				
				publicing(getAddress,userid)
				.then((res)=>{
					console.log(res);
					this.addressList=res.data;
				})
				.catch((err)=>{
					
				})
			}
		},
		//删除地址
		deleteAddress_method(index){
			let data={
				userid: this.userInfo.id,
				address_id:this.addressList[index].id
			};
			publicing(deleteAddress,data)
			.then((res)=>{
				this.addressList.splice(index,1)
			})
			.catch((err)=>{
				
			})
			
		},
		//增加地址
		addAddress_method(){
			this.userInfo = uni.getStorageSync("userInfo");
			if(this.userInfo)
			{
				uni.navigateTo({
				    url: './addSite'
				});
			}
			else{
				console.log('用户没有登录')
				// 弹出模态登录框
				this.$nextTick(()=>{
					this.$refs.mon.init();
				})
				
			}

		},
		//修改本地地址并且返回到上一级地址
		
		
		changeAddress(item){
			//返回上一级别
			console.log("修改当前缓存地址")
			this.$store.commit("UpdataAddress",item)
			uni.navigateBack({
				
			})
		}
	},
	onShow() {
			this.userInfo = uni.getStorageSync("userInfo");
			if(this.userInfo)
			{
				this.getAdderss_method()
			}
	},
	mounted(){
		
	}
}
